import { Component } from 'react/cjs/react.production.min';
import '../css/Header.css'
import jquery from 'jquery';
import $ from 'jquery';

class Header extends Component{
    
    componentDidMount(){
        $(document).ready(function(){
          $("#header #bottom .header_wrap .gnb,#header .sub_menu").hover(function(){
            $("#header .sub_menu").css("display","block")
          },function(){
            $("#header .sub_menu").css("display","none")
          })
          $("#header #bottom .header_wrap  .gnb ul li").hover(function(){
              $(this).find("a").css("color","#d7021b").addClass("active")
          },function(){
              $("#header #bottom .header_wrap  .gnb ul li").find("a").css("color","").removeClass("active")
          })
          $("#header .sub_menu .sub_menu_wrap .sub_gnb ul").hover(function(){
            var a= $(this).index()
            $("#header #bottom .header_wrap  .gnb ul li").eq(a).find("a").css("color","#d7021b").addClass("active")
            },function(){
                $("#header #bottom .header_wrap  .gnb ul li").find("a").css("color","").removeClass("active")
            })
          $("#header #top .header_wrap .right > ul > li.color").click(function(){
                $("#header #top .header_wrap .right > ul > li.color .arrow").css("transform","rotate(0deg)")
                $("#header #top .header_wrap .right ul li.color .box").hide()
                $(this).find("div").show()
                $(this).find("span").css("transform","rotate(180deg)")
            })
          $("#header #bottom .header_wrap .support .btn.search").click(function(){
              $(this).toggleClass("on")
              $("#header .search_wrap").toggleClass("show")
          })
        })
    }
    ModalMenu=()=>{
    this.props.ModalMenu()
    }
  render(){
    return(
      <div id="header">
          <div id="top">
             <div className='header_wrap'>
              <div className='left'>
                  <div className='zomm'>
                      <span></span>
                      <button className='btn plus'></button>
                      <button className='btn minus'></button>
                  </div>
                  <div className='sound'>
                  <span></span>
                      <button className='btn plus'></button>
                      <button className='btn minus'></button>
                      <button className='btn sound'></button>
                  </div>
              </div>
              <div className='right'>
                  <ul>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li className='color language'>
                        <a href='#'>Language</a><span className='arrow'></span>
                        <div className='box'>
                            <ul>
                                <li><a href="#">English</a></li>
                                <li><a href="#"></a></li>
                                <li><a href="#"></a></li>
                                <li><a href="#">Ting Vit</a></li>
                            </ul>
                        </div> 
                      </li>
                      <li className='color part'>
                          <a href='#'></a><span className='arrow'></span>
                          <div className='box'>
                              <ul>
                                  <li><a href='#'><div className='box_img'></div><span><br/> </span></a></li>
                                  <li><a href='#'><div className='box_img'></div><span><br/></span></a></li>
                                  <li><a href='#'><div className='box_img'></div><span><br/></span></a></li>
                                  <li><a href='#'><div className='box_img'></div><span>EDI<br/></span></a></li>
                                  <li><a href='#'><div className='box_img'></div><span><br/></span></a></li>
                                  <li><a href='#'><div className='box_img'></div><span><br/></span></a></li>
                              </ul>
                          </div>
                      </li>
                  </ul>
              </div>
             </div>
          </div>
          <div id="bottom">
              <div className='header_wrap'>
                  <div id="logo"><a href="#"></a></div>
                  <div className='gnb'>
                      <ul>
                          <li><a href="#"></a></li>
                          <li><a href="#">iN</a></li>
                          <li><a href="#"></a></li>
                          <li><a href="#"></a></li>
                          <li><a href="#"></a></li>
                      </ul>
                  </div>
                  <div className='support'>
                      <button className='btn search'></button>
                      <button onClick={this.ModalMenu} className='btn menu'></button>
                  </div>
              </div>
          </div>
            <div  className="sub_menu">
              <div className="sub_menu_wrap">
                    <div className='sub_gnb'>
                        <ul>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                        </ul>
                        <ul>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'>/</a></li>
                      <li><a className='link' href='#'></a></li>
                      <li><a href='#'></a></li>
                        </ul>
                        <ul>
                      <li><a href='#'></a></li>
                      <li><a className='link' href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a className='link' href='#'> Law</a></li> 
                        </ul>
                        <ul>
                      <li><a href='#'></a></li>
                      <li><a className='link' href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'>/</a></li>
                        </ul>
                        <ul>
                      <li><a href='#'></a></li>
                      <li><a href='#'>  </a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                      <li><a href='#'></a></li>
                        </ul>
                    </div>
                  </div>
                    <div className='sub_bottom'>
                        <ul>
                            <li><a href="#">  </a></li>
                            <li><a href="#"> </a></li>
                            <li><a href="#">/</a></li>
                            <li><a href="#">  </a></li>
                            <li><a href="#"></a></li>
                        </ul>
                    </div>
            </div>
            <div className='search_wrap'>
                <div className='inner'>
                    <div className='search'>
                        <input placeholder='   .'/>
                        <a className='total_search'></a>
                    </div>
                </div>
            </div>
      </div>
    )
  }
}

export default Header;
